import './css/main.css';

import StudentInformation from './components/StudentInformation';
import RepresentPhotos from './components/RepresentPhotos';

// create records of batches from 2018, predefine value of start batch year is currently 2012.
const student_info = new StudentInformation(2018);

student_info.LoadBatchYears();
student_info.LoadTableHeaders();
student_info.LoadCourses();
student_info.UpdateCoursesWRTBatch();
student_info.LoadBatchData();
// intially LoadBatchData
document.getElementById("batch-year").addEventListener("change", () => {

  student_info.UpdateCoursesWRTBatch();
  student_info.LoadBatchData();
})

document.getElementById("course-name").addEventListener("change", () => {
  // update table entries according with course selected 
  student_info.UpdateTableEntriesWRTCourse();
})

// section for slideshow and photo gallery

var photo_section = new RepresentPhotos();
photo_section.LoadSlideShowImages();
//setTimeout(photo_section.GiveMeDiv(),500);


document.getElementById("previous").addEventListener("click", () => {
  photo_section.PrevElement();
})

document.getElementById("next").addEventListener("click", () => {
  photo_section.NextElement();
})

