
import { API } from './API';
var all_slides;
export default class RepresentPhotos {
  constructor() {
    this.count = 0 ;
    this.all_slides = null;
  }

  LoadSlideShowImages() {
    var slides = document.querySelector(".photos-container .slides")

    var URL = 'https://shubham1010.github.io/jsonfiles/images/images_description.json';
    
    API.get(URL)
    .then(data => {
      for(var index=0; index<data.length; index++) {
        var newDiv = document.createElement("div");
        newDiv.className = "slide fade";
        newDiv.innerHTML =`<div class="slide-number">
             <h4>${index+1}/${data.length}</h4>
           </div>
           <div class="caption">               
             <h3>${data[index].heading}</h3>
             <p>${data[index].desc}</p>
           </div>
           <img src=${data[index].imgurl} />`;

        slides.appendChild(newDiv);
      }

    });

    setTimeout(this.GiveMeDiv, 500);

  }
 
  GiveMeDiv() {
    var slides = document.querySelector(".slides")
    all_slides = slides.querySelectorAll(".slide");
    for(var i=1; i<all_slides.length; i++) {
      all_slides[i].style.display = "none";
    }
  }

  NextElement() {
    if (all_slides == null)
      return;
    if (this.count+1 < all_slides.length) {
      all_slides[this.count++].style.display = "none";
      all_slides[this.count].style.display = "block";
    }
    else if (this.count+1 == all_slides.length) {
      all_slides[this.count].style.display = "none";
      this.count = 0;
      all_slides[this.count].style.display = "block";
    }
  }

  PrevElement() {
    if (this.count > 0) {
      all_slides[this.count--].style.display = "none";
      all_slides[this.count].style.display = "block";
    }
    else if (this.count == 0) {
      all_slides[this.count].style.display = "none";
      this.count = all_slides.length-1;
      all_slides[this.count].style.display = "block";
    }

  } 
}

